.biography {
  display: flex;
  flex-direction: row;

  @include _767 {
    flex-direction: column;
  }
}

.biography h2 {
  line-height: 40px;
  margin-bottom: 26px;
}

.biography__img {
  width: 307px;
  height: 307px;

  @include _767 {
    margin-bottom: 20px;
  }

  @include _596 {
    width: 210px;
    height: 210px;
  }

  @include _396 {
    width: 180px;
    height: 180px;
    margin: 0 auto;
    margin-bottom: 20px;
  }
}

.biography__list {
  width: 80%;
  margin-left: 60px;

  @include _767 {
    margin-left: 0;
    margin-bottom: 25px;
  }

  @include _496 {
    width: 100%;
  }
}

.biography__list p {
  margin-bottom: 20px;
  margin: 0;
  margin-bottom: 20px;
}
