.excerpt {
  display: flex;
  justify-content: space-between;
  width: 100%;
  padding-top: 40px;

  @include _992 {
    flex-direction: column;
    align-items: center;
  }
}

.excerpt__logo {
  width: 215px;
  height: 112px;

  @include _992 {
    width: 172px;
    height: 89.6px;
  }
}

.excerpt__logo img {
  width: auto;
  height: 100%;
}

.excerpt__item {
  position: relative;
  font-family: inherit;
  font-size: 25px;
  line-height: 30px;
  letter-spacing: -0.3px;
  color: #ffffff;
  margin: 0;
  margin-top: 5px;
  margin-left: 250px;

  @include _1570 {
    margin-left: 125px;
  }

  @include _1280 {
    font-size: 23px;
  }

  @include _992 {
    width: 630px;
    text-align: center;
    margin-top: 30px;
    margin-left: 0;
  }

  @include _767 {
    text-align: center;
  }

  @include _767 {
    width: 100%;
  }

  @include _496 {
    font-size: 20px;
  }
}

.excerpt__item span {
  display: block;
  font-size: 17px;
  font-weight: 100;
  margin-top: 20px;

  @include _496 {
    font-size: 15px;
    line-height: 24px;
  }
}

.excerpt__item--pencil::before {
  position: absolute;
  content: "";
  width: 146px;
  height: 152px;
  top: -20px;
  left: -160px;
  background-image: url("../img/pencils-big.png");
  background-repeat: no-repeat;
}

.excerpt__item--plane::before {
  position: absolute;
  content: "";
  width: 183px;
  height: 187px;
  top: -20px;
  left: -160px;
  background-image: url("../img/plane.png");
  background-repeat: no-repeat;
}

.excerpt__item--play::before {
  position: absolute;
  content: "";
  width: 75px;
  height: 75px;
  top: -20px;
  left: -160px;
  background-image: url("../img/icon-play.png");
  background-repeat: no-repeat;
}

.excerpt__item--face::before {
  position: absolute;
  content: "";
  width: 75px;
  height: 75px;
  top: -20px;
  left: -160px;
  background-image: url("../img/face.png");
  background-repeat: no-repeat;
}

.excerpt--about .excerpt__item {
  @include _767 {
    width: 70%;
  }

  @include _496 {
    width: 80%;
  }

  @include _396 {
    width: 100%;
  }
}

