/*  Сворачивание Меню  */
.header-nav__toggle {
  display: none; }
  @media (max-width: 992px) {
    .header-nav__toggle {
      position: absolute;
      display: block;
      top: 23px;
      left: 50px;
      width: 50px;
      height: 38px;
      background-color: transparent;
      padding: 0;
      border: none;
      outline: none;
      cursor: pointer;
      z-index: 3; } }
  @media (max-width: 596px) {
    .header-nav__toggle {
      left: 40px; } }
  @media (max-width: 596px) and (max-width: 396px) {
    .header-nav__toggle {
      left: 20px; } }

.header-nav--closed .header-nav__toggle::before,
.header-nav--closed .header-nav__toggle::before {
  display: block;
  content: "";
  position: absolute;
  top: 10px;
  left: calc(50% - 25px);
  width: 40px;
  height: 4px;
  background-color: white;
  box-shadow: 0 8px 0 0 white, 0 16px 0 0 white; }

.header-nav--closed .header-nav__toggle:hover::before,
.header-nav--closed .header-nav__toggle:focus::before {
  background-color: rgba(255, 255, 255, 0.7);
  box-shadow: 0 8px 0 0 rgba(255, 255, 255, 0.7), 0 16px 0 0 rgba(255, 255, 255, 0.7); }

.header-nav--closed .header-nav__toggle:active::before {
  background-color: rgba(255, 255, 255, 0.3);
  box-shadow: 0 8px 0 0 rgba(255, 255, 255, 0.3), 0 16px 0 0 rgba(255, 255, 255, 0.3); }

.header-nav--opened .header-nav__toggle {
  top: 38px;
  background-color: transparent; }

.header-nav--opened .header-nav__toggle::before,
.header-nav--opened .header-nav__toggle::after {
  content: "";
  position: absolute;
  top: 0px;
  left: 0;
  width: 36px;
  height: 4px;
  background-color: #f75a32; }

.header-nav--opened .header-nav__toggle::before {
  transform: rotate(45deg);
  box-shadow: none; }

.header-nav--opened .header-nav__toggle::after {
  transform: rotate(-45deg); }

.header-nav--opened .header-nav__toggle:hover::before,
.header-nav--opened .header-nav__toggle:hover::after,
.header-nav--opened .header-nav__toggle:focus::before,
.header-nav--opened .header-nav__toggle:focus::after {
  background-color: rgba(247, 90, 50, 0.7); }

.header-nav--opened .header-nav__toggle:active::before,
.header-nav--opened .header-nav__toggle:active::after {
  background-color: rgba(247, 90, 50, 0.3); }

