.lesson {
  max-width: 712px;
}

.lesson__back {
  width: 706px;
  height: 370px;
  background-color: #e6e6e6;
  transform: rotate(3deg);
  margin-top: 40px;
}

.lesson__img {
  position: relative;
  width: 706px;
  height: 370px;
  transform: rotate(-3deg);
}

.lesson__img--rotate {
  transform: rotate(-5deg);
}

.lesson__img img {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  object-fit: cover;
}

.lesson__caption {
  display: block;
  font-family: "Book Antiqua", "Times", serif;
  font-size: 17px;
  line-height: 18px;
  font-weight: 300;
  font-style: italic;
  text-align: center;
  margin-top: 25px;
  margin-bottom: 20px;
}

.lesson__list {
  margin-bottom: 58px;
}

.lesson__list h4 {
  font-family: "Book Antiqua", "Times", serif;
  font-size: 25px;
  font-weight: 700;
  color: #c2573d;
  margin: 0;
  margin-bottom: 25px;
}

.lesson__list p {
  margin: 0;
  margin-bottom: 20px;
}

.lesson__list li {
  position: relative;
  padding-left: 30px;
}

.lesson__list li:before {
  position: absolute;
  content: "—";
  left: 0;
}

.lesson__item {
  display: flex;
  align-items: center;
}

.lesson__list {
  margin-bottom: 55px;
}

.lesson__list ul {
  margin-top: 16px;
  margin-bottom: 20px;
}

.lesson__price {
  width: 373px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 0 auto;
  margin-top: 55px;
}

.lesson__price a,
.lesson__price p {
  font-family: "Book Antiqua","Times",serif;
  font-size: 42px;
  font-weight: 300;
  letter-spacing: -0.5px;
  color: #e74c3c;
  margin: 0;
  text-decoration: none;
}

.lesson__btn {
  width: 170px;
  height: 40px;
}
