.download {
  margin-left: 47px;
  margin-right: 60px;
}

.download__link {
  position: relative;
  color: #000000;
  text-decoration: none;
  margin: 0;
  cursor: pointer;
}

.download__link::before {
  position: absolute;
  content: "";
  top: 50%;
  transform: translateY(-50%);
  background-repeat: no-repeat;
}

.download__link--pdf::before {
  width: 53px;
  height: 24px;
  left: -57px;
  background-image: url("../img/icon-pdf.png");
}

.download__link--youtube::before {
  width: 36px;
  height: 25px;
  left: -45px;
  background-image: url("../img/icon-youtube.png");
}

.download__link--variant::before {
  width: 40px;
  height: 40px;
  left: -47px;
  background-image: url("../img/blank.jpg");
}

.download__link--mail::before {
  width: 40px;
  height: 40px;
  left: -47px;
  background-image: url("../img/icon-letter.jpg");
}

.download__link--time::before {
  width: 41px;
  height: 41px;
  left: -47px;
  background-image: url("../img/icon-call.jpg");
}
